<svelte:options immutable={true} />

<script>
  import { onMount, createEventDispatcher } from "svelte";

  import Toolbar from "./Toolbar.svelte";

  import { pannable } from "../utils/pannable.js";
  import { tapout } from "../utils/tapout.js";
  import { timeout } from "../utils/helper.js";
  import { Fonts } from "../utils/prepareAssets.js";

  export let size;
  export let text;
  export let lineHeight;
  export let x;
  export let y;
  export let fontFamily;
  export let pageScale = 1;

  const Families = Object.keys(Fonts);
  const dispatch = createEventDispatcher();
  let startX;
  let startY;
  let editable;
  let _size = size;
  let _lineHeight = lineHeight;
  let _fontFamily = fontFamily;
  let dx = 0;
  let dy = 0;
  let operation = "";

  function handlePanMove(event) {
    dx = (event.detail.x - startX) / pageScale;
    dy = (event.detail.y - startY) / pageScale;
  }

  function handlePanEnd(event) {
    if (dx === 0 && dy === 0) {
      return editable.focus();
    }
    dispatch("update", {
      x: x + dx,
      y: y + dy,
    });
    dx = 0;
    dy = 0;
    operation = "";
  }

  function handlePanStart(event) {
    startX = event.detail.x;
    startY = event.detail.y;
    operation = "move";
  }

  function onFocus() {
    operation = "edit";

    const nav = document.querySelector(".toolbar");

    if (!nav.classList.contains("toolbar--expand")) {
      openDrawer();
    }
  }

  async function onBlur() {
    if (operation !== "edit" || operation === "tool") return;
    editable.blur();
    sanitize();
    dispatch("update", {
      lines: extractLines(),
      width: editable.clientWidth,
    });

    operation = "";

    const nav = document.querySelector(".toolbar");

    if (nav.classList.contains("toolbar--expand")) {
      openDrawer();
    }
  }

  async function onPaste(e) {
    // get text only
    const pastedText = e.clipboardData.getData("text");
    document.execCommand("insertHTML", false, pastedText);
    // await tick() is not enough
    await timeout();
    sanitize();
  }

  function onKeydown(e) {
    const childNodes = Array.from(editable.childNodes);
    if (e.keyCode === 13) {
      // prevent default adding div behavior
      e.preventDefault();
      const selection = window.getSelection();
      const focusNode = selection.focusNode;
      const focusOffset = selection.focusOffset;
      // the caret is at an empty line
      if (focusNode === editable) {
        editable.insertBefore(
          document.createElement("br"),
          childNodes[focusOffset]
        );
      } else if (focusNode instanceof HTMLBRElement) {
        editable.insertBefore(document.createElement("br"), focusNode);
      }
      // the caret is at a text line but not end
      else if (focusNode.textContent.length !== focusOffset) {
        document.execCommand("insertHTML", false, "<br>");
        // the carat is at the end of a text line
      } else {
        let br = focusNode.nextSibling;
        if (br) {
          editable.insertBefore(document.createElement("br"), br);
        } else {
          br = editable.appendChild(document.createElement("br"));
          br = editable.appendChild(document.createElement("br"));
        }
        // set selection to new line
        selection.collapse(br, 0);
      }
    }
  }

  function onFocusTool() {
    operation = "tool";
  }

  async function onBlurTool() {
    if (operation !== "tool" || operation === "edit") return;
    dispatch("update", {
      lines: extractLines(),
      lineHeight: _lineHeight,
      size: _size,
      fontFamily: _fontFamily,
    });

    operation = "";

    const nav = document.querySelector(".toolbar");

    if (nav.classList.contains("toolbar--expand")) {
      openDrawer();
    }
  }

  function sanitize() {
    let weirdNode;
    while (
      (weirdNode = Array.from(editable.childNodes).find(
        (node) => !["#text", "BR"].includes(node.nodeName)
      ))
    ) {
      editable.removeChild(weirdNode);
    }
  }

  function onChangeFont() {
    dispatch("selectFont", {
      name: _fontFamily,
    });
  }

  function render() {
    editable.innerHTML = text;
    editable.focus();
  }

  function extractLines() {
    const nodes = editable.childNodes;
    const lines = [];
    let lineText = "";
    for (let index = 0; index < nodes.length; index++) {
      const node = nodes[index];
      if (node.nodeName === "BR") {
        lines.push(lineText);
        lineText = "";
      } else {
        lineText += node.textContent;
      }
    }
    lines.push(lineText);
    return lines;
  }

  function onDelete() {
    openDrawer();
  }

  function openDrawer() {
    const nav = document.querySelector(".toolbar");
    const hide = document.querySelector(".hide");
    const tools = document.querySelector(".tools");

    if (operation === "tool") {
      dispatch("delete");

      if (!nav.classList.contains("toolbar--expand")) {
        return;
      }
    }

    if (operation == "" && nav.classList.contains("toolbar--expand")) {
      nav.classList.remove("toolbar--expand");
      hide.classList.add("hidden");
      tools.classList.remove("justify-around");
      tools.classList.add("justify-center");
    } else if (nav.classList.contains("toolbar--expand")) {
      nav.classList.remove("toolbar--expand");
      hide.classList.add("hidden");
      tools.classList.remove("justify-around");
      tools.classList.add("justify-center");

      dispatch("delete");
    } else {
      nav.classList.add("toolbar--expand");
      hide.classList.remove("hidden");
      tools.classList.add("justify-around");
      tools.classList.remove("justify-center");
    }
  }

  onMount(render);
</script>

{#if operation && operation !== "move"}
  <Toolbar>
    <div
      use:tapout
      on:tapout={onBlurTool}
      on:mousedown={onFocusTool}
      on:touchstart={onFocusTool}
      class="h-full flex justify-around items-center bg-white"
    >
      <div class="w-56 mr-2 flex items-center border border-black">
        <div class="w-full h-full">
          <select
            bind:value={_fontFamily}
            on:change={onChangeFont}
            class="bg-white w-full h-full outline-none"
          >
            {#each Families as family}
              <option value={family}>{family}</option>
            {/each}
          </select>
          <div
            class="pointer-events-none absolute inset-y-0 right-0 flex
            items-center px-2 text-gray-700"
          />
        </div>
      </div>
      <div class="mr-2 flex items-center w-16 border border-black h-8">
        <input
          type="number"
          min="12"
          max="120"
          step="1"
          class="h-full w-full text-center flex-shrink-0 rounded-sm"
          bind:value={_size}
        />
      </div>
      <div
        on:click={onDelete}
        class="w-5 h-5 rounded-full bg-white cursor-pointer"
      >
        <img
          class="w-full h-full"
          src="./delete-icon.svg"
          alt="delete object"
        />
      </div>
    </div>
  </Toolbar>
{/if}
<div
  use:tapout
  on:tapout={onBlur}
  class="absolute left-0 top-0 select-none"
  style="transform: translate({x + dx}px, {y + dy}px);"
>
  <div
    use:pannable
    on:panstart={handlePanStart}
    on:panmove={handlePanMove}
    on:panend={handlePanEnd}
    class="absolute w-full h-full cursor-grab border border-black
    border-gray-500"
    class:cursor-grab={!operation}
    class:cursor-grabbing={operation === "move"}
    class:editing={["edit", "tool"].includes(operation)}
  />
  <div
    bind:this={editable}
    on:focus={onFocus}
    on:keydown={onKeydown}
    on:paste|preventDefault={onPaste}
    contenteditable="true"
    spellcheck="false"
    class="outline-none whitespace-no-wrap"
    style="font-size: {_size}px; font-family: '{_fontFamily}', serif;
    line-height: {_lineHeight}; -webkit-user-select: text;"
  />
</div>

<style>.editing {
  pointer-events: none;
}</style>
